.header
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color:rgb(0,0,0,0.85);
    transition: 0.5s;
}

.nav-menu
{
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
    list-style: none;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}